import generateKey from '../utils/generateKey';
// 所有Web的Method回调挂在window.XHSCallBack对象下
// @ts-ignore
if (process.env.BROWSER) {
    window.XHSCallbacks = window.XHSCallbacks || {};
}
/**
 * Web平台的Bridge方法回调注册
 * @param method Bridge方法名字
 * @param callback Bridge方法的回调
 */
export function addCallback(method, callback) {
    const key = `${method}_${generateKey()}`;
    // native回调
    window.XHSCallbacks[key] = callback;
    return `window.XHSCallbacks.${key}`;
}
