let loaded = false;
export function importJsSDK(sdkUrl) {
    return new Promise((resolve, reject) => {
        if (loaded) {
            resolve();
            return;
        }
        const jweixinScript = document.createElement('script');
        jweixinScript.setAttribute('src', sdkUrl);
        jweixinScript.onload = () => {
            loaded = true;
            resolve();
        };
        jweixinScript.onerror = () => {
            reject('js sdk load error');
        };
        document.head.appendChild(jweixinScript);
    });
}
