import { invoke, } from '@xhs/ozone-schema';
/**
   * 分享到私信, 目前支持 goodsDetail 和 general 两个类型
   */
export function shareToFriend(args) {
    const clientArgs = { contentType: 'text', ...args };
    return invoke('shareToFriend', clientArgs);
}
/**
 * 分享到 QQ 好友, 支持文本、图片、链接、小程序四种类型，文本分享类型仅支持 iOS。只支持静态图片
 */
export function shareToQQ(args) {
    return invoke('shareToQQ', args);
}
/**
 * 分享到 QQ 空间, 支持图片、链接（网页类型）两种类型。只支持静态图片
 */
export function shareToQQSpace(args) {
    return invoke('shareToQQSpace', args);
}
/**
 * 分享到新浪微博, 支持文本、图片、链接（图文混排）三种类型。只支持静态图片
 */
export function shareToSinaWeibo(args) {
    return invoke('shareToSinaWeibo', args);
}
/**
 * 分享到微信好友, 支持文本、图片、链接（微信网页类型的分享）、小程序四种类型。 除了 emoji 分享以外，其他分享只支持静态图片
 */
export function shareToWechatSession(args) {
    return invoke('shareToWechatSession', args);
}
/**
 * 分享到微信朋友圈, 支持文本、图片、链接（网页类型）三种类型。 只支持静态图片
 */
export function shareToWechatTimeline(args) {
    return invoke('shareToWechatTimeline', args);
}
