/**
 * 生成分享链接，移除敏感信息
 * @param link "https://red-heart.xiaohongshu.com/ranks/home?fullscreen=true&statusBar=1&naviHidden=yes&disableNativeLoading=yes&disableDragBack=yes&page_source=search_result_notes.onebox&search_id=1933FADCB3EE95A5CEBAAEA00E6C33E3&isUnicomKing=false&themeType=default&owl_debug=&sid=session.1571206914433948451770&ssid=X6433esession.1571206914433948451770&clientsource=app&platform=iOS"
 */
export default function genShareUrl(link = window.location.href) {
    const url = new URL(link);
    url.searchParams.delete('sid');
    url.searchParams.delete('ssid');
    url.searchParams.delete('clientsource');
    url.searchParams.delete('platform');
    return url;
}
