export async function shareContent({ shareTo, type = ShareContentType.Link, title, desc = '', link, imgUrl, base64string = '', extension = {}, userName, }) {
    if (!process.env.BROWSER) {
        return;
    }
    const shareUrl = genShareUrl(link);
    const bridgeConfig = {
        type: shareTo,
        title,
        content: desc,
        linkurl: shareUrl.href,
        imageurl: imgUrl,
    };
    if (type === ShareContentType.WeChatMiniProgram) {
        shareUrl.searchParams.set('canShare', 'true');
        // 分享至小程序的 webview link
        let mpWebviewLink = shareUrl.href;
        mpWebviewLink = mpWebviewLink.includes('?')
            ? encodeURIComponent(mpWebviewLink)
            : mpWebviewLink;
        bridgeConfig.type = type;
        bridgeConfig.extension = {
            miniprogram: {
                title,
                desc,
                path: `/main/webview/index?link=${mpWebviewLink}`,
                webpageurl: shareUrl.href,
                thumb: imgUrl,
                username: userName || MiniprogramUserName.WechatRed,
            },
        };
    }
    else if (type === ShareContentType.Image) {
        bridgeConfig.contentType = type;
        bridgeConfig.base64string = base64string;
    }
    else if (type === ShareContentType.Text) {
        bridgeConfig.contentType = type;
    }
    if (shareTo === ShareTo.ShareToFriend) {
        bridgeConfig.extension = extension;
    }
    try {
        await shareContentBridge(bridgeConfig);
    }
    catch (error) {
        // eslint-disable-line
    }
}
